<template>
  <div>
    <v-toolbar color="grey lighten-2">
      创建新的进程<v-icon dark right color="primary"> mdi-plus-circle </v-icon>
    </v-toolbar>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step :complete="e1 > 1" step="1">
          工作目录选择
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="e1 > 2" step="2"> 启动命令 </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step step="3"> 配置 </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card class="mb-12">
            <div style="overflow-y: auto">
              <!-- <v-treeview
                    v-model="activeNode"
                    @update:active="updateActiveNode"
                    @update:open="select"
                    activatable
                    color="warning"
                    :items="items"
                  ></v-treeview> -->
              <v-text-field
                v-model="createProcessForm.cwd"
                name="请输入目录路径"
                label="工作目录"
              ></v-text-field>
            </div>
          </v-card>
          <v-btn color="primary" @click="e1 = 2"> 下一步 </v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card class="mb-12"
            ><v-text-field
              v-model="createProcessForm.cmd"
              name="请输入启动命令"
              label="启动命令"
            ></v-text-field
          ></v-card>

          <v-btn color="primary" @click="e1 = 3"> 下一步 </v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <v-card class="mb-12" height="200px">
            <v-text-field
              v-model="createProcessForm.name"
              name="请输入名称"
              label="名称"
            ></v-text-field>
            <v-select v-model="createProcessForm.termType" :items="['pty','std']" label="终端类型"></v-select>
            <v-row>
              <v-col cols="12" md="4">
                <v-switch
                  v-model="createProcessForm.autoRestart"
                  label="自动重启"
                  color="blue"
                  hide-details
                ></v-switch
              ></v-col>

              <v-col cols="12" md="4">
                <v-switch
                  v-model="createProcessForm.push"
                  label="状态推送"
                  color="blue"
                  hide-details
                ></v-switch
              ></v-col>

              <v-col cols="12" md="4">
                <v-switch
                  v-model="createProcessForm.logReport"
                  label="日志上报"
                  color="blue"
                  hide-details
                ></v-switch>
              </v-col>
            </v-row>
          </v-card>

          <v-btn color="primary" @click="create"> 完成 </v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>
<script>
import { getFileList } from "../api/file";
import { postProcessConfig } from "../api/process";
export default {
  model: {
    // 定义model
    prop: "fatherValue", // 父组件v-model绑定的值传递给props中的fatherValue
    event: "childValueChange",
    // 通过emit触发childValueChange将内部值传递给父组件v-model绑定的值
  },
  data() {
    return {
      tree: "",
      e1: 1,
      activeNode: [],
      items: [
        {
          id: "",
          name: "/",
          children: [{}],
        },
      ],
      createProcessForm: {
        termType: "pty",
      },
      childValue: this.fatherValue,
    };
  },

  props: {},
  created() {
    getFileList("/").then((resp) => {
      this.items[0].children = resp.data
        .filter((item) => {
          return item.isDir;
        })
        .map((item) => {
          return { id: item.name, name: item.name, children: [{}] };
        });
    });
  },
  methods: {
    childInputChange() {
      // 通过$emit触发childValueChange（model内定义）事件，将内部值传递给给父组件
      this.childValue = false;
      this.$emit("childValueChange", this.childValue);
    },
    create() {
      postProcessConfig(this.createProcessForm).then((resp) => {
        if (resp.code === 0) {
          this.$message.success("创建成功");
          this.$parent.getProcessList();
        }
      });
      this.childInputChange();
    },
    updateActiveNode(node) {
      this.activeNode = node;
      if (node) {
        const parentNode = this.getParentNode(node);
        console.log("Parent Node:", parentNode);
      }
    },
    getParentNode(node) {
      // 根据当前节点的id，找到父节点
      return this.treeData.find((item) => {
        return item.children.some((child) => child.id === node.id);
      });
    },
    select(node) {
      if (node == "") return;
      console.log(this.activeNode);
      this.getFiles(node);
    },
    active() {
      // console.log(node);
    },
    getFiles(pathAry) {
      var target = this.items[0];
      // console.log(target);
      pathAry.forEach((element) => {
        target.children.forEach((item) => {
          // console.log(element)
          if (item.name == element) {
            target = item;
          }
        });
      });
      getFileList("/" + pathAry.filter((e) => e != "").join("/")).then(
        (resp) => {
          target.children = resp.data
            .filter((item) => {
              return item.isDir;
            })
            .map((item) => {
              return { id: item.name, name: item.name, children: [{}] };
            });
        }
      );
    },
  },
};
</script>
